import MealItemForm from "../Meals/MealItemForm";
import Modal from "../UI/Modal";
import classes from "./Cart.module.css";
import React, { useContext,useState } from "react";
import CartContext from "../../Store/cart-context";
import CartItem from "./CartItem";
import Checkout from "./Checkout";

const Cart = (props) => {
  const[isCheckout, setIsCheckout] = useState(false);
  const[isSubmitting,setIsSubmitting] = useState(false);
  const[didSubmit,setDidSubmit] = useState(false);

  const cartCtx = useContext(CartContext);
  const totalAmount = `Rs. ${cartCtx.totalAmount.toFixed(2)}`;
  const isItem = cartCtx.items.length > 0;
  const cartItemRemoveHandler = (id) => {
    cartCtx.removeItem(id);
  };
  const cartItemAddHandler = (item) => {
    cartCtx.addItem({...item,amount:1});
  };

  const orderHandler = () =>{
    setIsCheckout(true);
  }

const orderSubmitHandler = async (userData) =>{
  setIsSubmitting(true);

  await fetch('https://thefoodmelody-26fa9-default-rtdb.firebaseio.com/orders.json',{
    method:'POST',
    body: JSON.stringify({
      user: userData,
      orderedItems: cartCtx.items
    })
  });
  setIsSubmitting(false);
  setDidSubmit(true);
  cartCtx.clearCart();
};
  const cartItems = (
    <ul className={classes['cart-items']}>
      {cartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          name={item.name}
          price={item.price}
          amount={item.amount}
          onRemove={cartItemRemoveHandler.bind(null,item.id)}
          onAdd={cartItemAddHandler.bind(null,item)}
        />
      ))}
    </ul>
  );

  const modalActions = <div className={classes.actions}>
  <button className={classes["button--alt"]} onClick={props.onHideCart}>
    Close
  </button>
  {isItem && <button className={classes.button} onClick={orderHandler}>Order</button>}
</div>;

const cartModelContent = (<React.Fragment>
  {cartItems}
      <div className={classes.total}>
        <span>Total Amount</span>
        <span>{totalAmount}</span>
      </div>
      {isCheckout && <Checkout onConfirm ={orderSubmitHandler}  onCancel={props.onHideCart}/>}
      {!isCheckout && modalActions}
</React.Fragment>);

const isSubmittingModalContent = <p>Sending order data...</p>
const didSubmitModalContent =<React.Fragment><p>Successfully sent the order!</p>
<div className={classes.actions}>
<button className={classes.button} onClick={props.onHideCart}>
  Close
</button>
</div></React.Fragment>
  return (
    <Modal onClose={props.onHideCart}>
      {!isSubmitting && !didSubmit  && cartModelContent}
      {isSubmitting && isSubmittingModalContent}
      {!isSubmitting && didSubmit && didSubmitModalContent}
    </Modal>
  );
};

export default Cart;
